@import "../../../assets/sass/index";
.card {
  position: relative;
  padding: 1rem;
  border-radius: $custom-border-radius;
  background: #f8f8fa;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  transform-origin: bottom;
  transition: all 0.5s;

  &:hover {
    background: #ffffff;
    box-shadow: 4px 4px 30px 2px rgba(146, 153, 173, 0.5);
    animation-name: bounce;
    animation-timing-function: ease;
  }
}

@keyframes bounce {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
}

.content {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  row-gap: 0.5rem;
  @include xl {
    font-size: 1.2rem;
    row-gap: 1rem;
  }
}

.bannerImg {
  width: 100%;
  border-radius: $custom-border-radius;
}

.projectTitle {
  font-size: 1rem;
  font-weight: 600;
  @include xl {
    font-size: 1.5rem;
  }
}
